.show-all {
  width: 280px;
  margin: 0 auto;
  text-align: center;

  &::before {
    content: url("../../img/plus.svg");
    display: block;
    margin: 0 auto 23px auto;
    width: 30px;
    height: 30px;
  }
}

.show-all__title {
  display: block;
  margin: 0;
  margin-bottom: 8px;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.show-all__text {
  display: block;
  margin: 0;
  margin-bottom: 21px;
  padding: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}
